import React from "react"

import { Icon, Button, Flex, Heading, IconButton, useColorMode, useMediaQuery, Show, Hide, Box, useDisclosure, Grid, } from '@chakra-ui/react'
import { HiTerminal } from 'react-icons/hi'
import { CloseCircle, CloseSquare, HambergerMenu, Moon, Sun1 } from "iconsax-react";
import { ReactComponent as Logo } from '../../assets/logo.svg'
import { AnimatePresence, motion } from "framer-motion";

export default function Navbar() {
    const { colorMode, toggleColorMode } = useColorMode()
    const [isMd] = useMediaQuery('(max-width: 768px)')
    const ham = useDisclosure()
    const buttons = [
        {
            title: "About Me",
            onClick: () => { }
        },
        {
            title: "Skills",
            onClick: () => { }
        },
        {
            title: "Projects",
            onClick: () => { }
        },
        {
            title: "Links",
            onClick: () => { }
        },
    ]
    return (
        <>
            <Flex flexDir="column">
                <AnimatePresence>
                    {ham.isOpen &&
                        buttons.map((btn, index) => {
                            return (
                                <motion.div
                                    initial={{ x: 50, opacity: 0, height: 0 }}
                                    animate={{ x: 0, opacity: 1, height: "auto" }}
                                    transition={{ duration: 0.1, delay: index * 0.05 }}
                                    key={index}
                                    exit={{
                                        x: -50,
                                        opacity: 0,
                                        transition: { duration: 0.1, delay: 0.05 * (5 - index) },
                                        height: 0
                                    }}
                                    onClick={btn.onClick}
                                >
                                    <Button borderRadius={0} w="100%">{btn.title}</Button>
                                </motion.div>
                            );
                        })}
                </AnimatePresence>

            </Flex>
            <Flex h="60px" align="center" className="nav" justify="space-between" gap={{ base: 2, sm: 0 }} pos="relative" >
                <Flex gap={0} justify="center" align="center" >
                    {
                        isMd ?

                            <Icon boxSize={12} as={Logo} color={colorMode === "light" ? "black" : "white"} />
                            : <HiTerminal size={36} />
                    }
                    <Heading fontWeight={800} fontSize={24} >{isMd ? "nurag" : "/home/anurag/portfolio"}</Heading>
                </Flex>
                <Flex gap={2} justify="center" align="center" >
                    <Show above="sm">
                        <AnimatePresence>
                            {buttons.map((btn, index) => {
                                return (
                                    <motion.div
                                        initial={{ x: 50, opacity: 0, height: 0 }}
                                        animate={{ x: 0, opacity: 1, height: "auto" }}
                                        transition={{ duration: 0.1, delay: index * 0.05 }}
                                        key={index}
                                        exit={{
                                            x: -50,
                                            opacity: 0,
                                            transition: { duration: 0.1, delay: 0.05 * (5 - index) },
                                            height: 0
                                        }}
                                        onClick={btn.onClick}
                                    >
                                        <Button borderRadius="200vmax" w="100%">{btn.title}</Button>
                                    </motion.div>
                                )
                            })}
                        </AnimatePresence>
                    </Show>
                    <IconButton onClick={toggleColorMode} isRound>
                        {
                            colorMode === 'light' ? <Moon /> : <Sun1 />
                        }
                    </IconButton>
                    <Hide above="sm">
                        <IconButton onClick={ham.onToggle} isRound >
                            {
                                ham.isOpen ? <CloseCircle /> : <HambergerMenu />

                            }
                        </IconButton>
                    </Hide>
                </Flex>
            </Flex>
        </>
    )
}
